.mobile {
	background: url('../img/bg/mobile_bg.jpg') center (center / cover) no-repeat;
	min-height: 864px;
	padding: 48px 0 61px 0;
	&_opportunities {
		display: flex;
		justify-content: center;
		align-items: center;
		background-color: black;
		width: 112px;
		height: 24px;
		text-transform: uppercase;
		font-size: 11px;
		font-weight: 100;
		color: white;
		text-align: center;
		margin: 0 auto; }
	&_title {
		font-size: 38px;
		color: rgba(34, 34, 34, 255);
		text-align: center;
		font-weight: 700;
		text-transform: uppercase;
		margin-top: 38px;
		line-height: 28px; }
	&_text {
		font-weight: 400;
		font-size: 17px;
		color: rgba(34, 34, 34, 255);
		text-align: center;
		margin-top: 18px;
		line-height: 17px;
		margin-bottom: 32px; }
	&_item {
		height: 280px;
		width: 320px;
		position: relative;
		margin-bottom: 19px;
		&_1 {
			background: url('../img/mobile_img_1.png'); }
		&_2 {
			background: url('../img/mobile_img_2.png'); }
		&_3 {
			background: url('../img/mobile_img_3.png'); }
		&_4 {
			background: url('../img/mobile_img_4.png'); }
		&_5 {
			background: url('../img/mobile_img_5.png'); }
		&_6 {
			background: url('../img/mobile_img_6.png'); }
		&_text {
			padding-top: 92px;
			text-align: center;
			font-size: 24px;
			font-weight: 400;
			color: rgba(255, 255, 255, 255);
			text-transform: uppercase; }
		&_plus {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			height: 27px;
			width: 27px;
			background-color: black;
			border-radius: 100%;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: #1EACC7;
				transition: 0.4s all; }
			&:after {
				position: absolute;
				transform: translateX(50%);
				right: 50%;
				content: '';
				display: block;
				width: 2px;
				height: 14px;
				background-color: #1EACC7;
				transition: 0.4s all; }
			&:hover {
				&:before, &:after {
					background-color: red; } } } } }




